<template>
  <div class="user-edit-container">
    <!-- Header 区域 -->
    <van-nav-bar title="编辑资料" left-arrow @click-left="$router.back()" fixed />

    <!-- 用户资料 -->
    <van-cell-group class="action-card">
      <van-cell title="头像" is-link center>
        <template #default>
          <van-image round class="avatar" :src="profileObj.photo" @click="imageClickFn" />
          <!-- file 选择框 -->
          <input type="file" ref="iptFile" v-show="false" accept="image/*" @change="onFileChange" />
        </template>
      </van-cell>
      <van-cell title="名称" is-link :value="profileObj.name" @click="nameClickFn" />
      <van-cell title="生日" is-link :value="profileObj.birthday" @click="birthdayClickFn"/>
    </van-cell-group>

    <!-- 姓名修改弹窗 -->
    <van-dialog v-model="show" title="标题" show-cancel-button :before-close="beforeCloseFn">
      <input type="text" v-fofo v-model="inputUserName" />
    </van-dialog>

    <!-- 时间选择器 -->
    <van-popup v-model="dataTimePickerShow" position="bottom" :style="{ height: '50%' }">
    <van-datetime-picker
     v-model="currentDate"
     type="date" title="选择年月日"
     :min-date="minDate"
     :max-date="maxDate"
     @cancel="dateCancelFn"
     @confirm="dateConfirmFn" />
    </van-popup>
  </div>
</template>

<script>
import { userProfileAPI, updateUserPhotoAPI, updateUserProfileAPI } from '@/api'
import { formatDate } from '@/utils/date.js'
import Notify from '@/ui/Notify.js'
import { mapMutations } from 'vuex'
export default {
  name: 'UserEdit',
  data () {
    return {
      profileObj: {}, // 用户基本资料
      show: false, // 控制姓名修改弹窗隐藏/显示
      inputUserName: '', // 修改名字 - 弹出框输入框绑定值
      // 时间选择器
      minDate: new Date(1990, 0, 1),
      maxDate: new Date(), // 默认（不填值）就是当前时间
      currentDate: new Date(),
      dataTimePickerShow: false // 日期选择器 - 弹出层显示/隐藏
    }
  },
  async created () {
    const res = await userProfileAPI()
    console.log(res)
    this.profileObj = res.data.data
  },
  methods: {
    ...mapMutations(['SET_USERPHOTO']),
    // 文件选择改变 - 事件
    async onFileChange (e) {
      if (e.target.files.length === 0) return
      console.log(e.target.files[0])
      // 创建 FormData 对象
      const theFd = new FormData()
      theFd.append('photo', e.target.files[0])
      const res = await updateUserPhotoAPI(theFd)
      console.log(res)
      this.profileObj.photo = res.data.data.photo
      this.SET_USERPHOTO(res.data.data.photo)
    },
    // 图片 - 点击事件
    imageClickFn () {
      this.$refs.iptFile.click()
    },
    // 姓名 - 点击事件
    nameClickFn () {
      this.show = true
      this.inputUserName = this.profileObj.name
    },
    // 姓名 - 确认框 - 关闭前回调函数
    async beforeCloseFn (action, done) {
      // console.log(action)
      if (action === 'confirm') {
        // 点确定
        const reg = /^[A-Za-z0-9\u4e00-\u9fa5]{1,7}$/
        if (reg.test(this.inputUserName) === true) {
          //  通过了校验
          await updateUserProfileAPI({
            name: this.inputUserName
          })
          this.profileObj.name = this.inputUserName // 数据回显
          done()
        } else {
          // 没通过校验，给用户提示
          Notify({ type: 'warning', message: '用户名字只能是1-7位中英数字组合' })
          done(false)
        }
      } else {
        // 点取消
        done() // 弹窗关闭
      }
    },
    // 生日单元格 - 点击事件
    birthdayClickFn () {
      this.dataTimePickerShow = true
      this.currentDate = new Date(this.profileObj.birthday)
    },
    // 日期选择器 - 取消事件
    dateCancelFn () {
      this.dataTimePickerShow = false
    },
    // 日期选择器 - 完成确认时间
    async dateConfirmFn () {
      await updateUserProfileAPI({
        birthday: formatDate(this.currentDate)
      })
      this.dataTimePickerShow = false
      this.profileObj.birthday = formatDate(this.currentDate)// 单元格同步
    }
  }
}
</script>

<style lang="less" scoped>
.user-edit-container {
  padding-top: 46px;
  .avatar {
    width: 50px;
    height: 50px;
  }
}
::v-deep .van-dialog__content {
  text-align: center;
  input {
    padding: 0;
    outline: none;
    border: none;
    text-align: center;
  }
}
</style>
